import React from "react";

const GoalPoint = () => (
  <div
    style={{
      display: "flex",
      flexDirection: "row",
      padding: "0 3%",
      margin: "2% 0",
    }}
  >
    <div style={{ width: "50%" }}>
      <h2>Carbon Foot Print</h2>
      <p>2340</p>
    </div>
    <div style={{ width: "50%", textAlign: "right" }}>
      <h2>Target/Goal</h2>
      <p>10000</p>
    </div>
  </div>
);

export default GoalPoint;
