import React from "react";
import { Box, Modal, Button, Typography } from "@mui/material";
import CustomModalHeader from "./CustomModalHeader";
import CustomModalBody from "./CustomModalBody";
import CustomTable from "./CustomTable";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

const CustomModal = ({ openOrganization, handleCloseOrganization }) => {
  const handleClose = () => handleCloseOrganization();

  return (
    <div>
      <Modal
        open={openOrganization}
        onClose={handleClose}
        aria-labelledby="modal-title"
        aria-describedby="modal-description"
      >
        <Box sx={style}>
          <CustomModalHeader onClose={handleClose}/>
          <CustomModalBody/>
          <CustomTable/>
          <Button
            variant="contained"
            color="secondary"
            onClick={handleClose}
            sx={{ mt: 2 }}
          >
            Close
          </Button>
        </Box>
      </Modal>
    </div>
  );
};

export default CustomModal;
