import React from "react";
import { Container, Paper } from "@mui/material";
import ProfileImage from "./profileImage";
import GoalPoint from "./GoalPoint";
import ButtonJS from "./Button";
import PieChartComponent from "./PieChartComponent";
import CustomModal from "./CustomModal";
import CustomModalAddNew from "./CustomModalAddNew";

const MainContent = ({openAdd, closeAdd}) => {

    const [openOrganization, setOpenOrganization] = React.useState(false);

    const handleOpenOrganization = () => {
        setOpenOrganization(true);
    }

    const handleCloseOrganization = () => {
        setOpenOrganization(false);
    }

    const handleCloseAddNew = () => {
        closeAdd(false);
    }

    return(
  <Container>
    <Paper elevation={3} style={{ padding: "20px", marginTop: "20px" }}>
      <ProfileImage src={ProfileImage} alt={"Profile"} />
      <GoalPoint/>
      <PieChartComponent/>
      <ButtonJS handleOpenOrganization={handleOpenOrganization}/>
      <CustomModal openOrganization={openOrganization} handleCloseOrganization={handleCloseOrganization}/>
      {/* openAdd */}
      <CustomModalAddNew openAdd={openAdd} handleCloseAddNew={handleCloseAddNew}/>
    </Paper>
  </Container>
)};

export default MainContent;
