import React from "react";

const CustomModalBody = () => (
  <div
    style={{
      display: "flex",
      flexDirection: "row",
      padding: "0 3%",
      margin: "2% 0",
      border: '0.5px solid black',
      boxShadow: '0 0 1 1'
    }}
  >
    <div style={{ width: "50%" }}>
      <h5>Organization CFP</h5>
      <p>2340</p>
    </div>
    <div style={{ width: "50%", textAlign: "right" }}>
      <h5>Number of employees</h5>
      <p>20</p>
    </div>
  </div>
);

export default CustomModalBody;
