import React, { useState } from "react";
import { Box, Modal, Button, Typography } from "@mui/material";
import { FormControl, InputLabel, MenuItem, Select } from "@mui/material";
const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

const CustomModalAddNew = ({ openAdd, handleCloseAddNew }) => {
  const handleClose = () => handleCloseAddNew();
  const [selectedHouse, setSelectedHouse] = useState("");

  const handleChangeHouse = (event) => {
    setSelectedHouse(event.target.value);
  };

  const [selectedModeTransport, setSelectedModeTransport] = useState("");

  const handleChangeModeTransport = (event) => {
    setSelectedModeTransport(event.target.value);
  };
  return (
    <div>
      <Modal
        open={openAdd}
        onClose={handleClose}
        aria-labelledby="modal-title"
        aria-describedby="modal-description"
      >
        <Box sx={style}>
          <FormControl sx={{ margin: '2%'}}>
            <InputLabel id="dropdown-label">House Hold</InputLabel>
            <Select
              labelId="dropdown-label"
              id="dropdown"
              value={selectedHouse}
              onChange={handleChangeHouse}
              label="House Hold"
            >
              <MenuItem value="Area">Area</MenuItem>
              <MenuItem value="People_Count">People Count</MenuItem>
              <MenuItem value="Appliances">Appliances</MenuItem>
            </Select>
          </FormControl>
          <FormControl sx={{ margin: '2%'}}>
            <InputLabel id="dropdown-label">Mode of Transport</InputLabel>
            <Select
              labelId="dropdown-label"
              id="dropdown"
              value={selectedModeTransport}
              onChange={handleChangeModeTransport}
              label="Mode of Transport"
            >
              <MenuItem value="2-Wheeler">2-Wheeler</MenuItem>
              <MenuItem value="4-Wheeler">4-Wheeler</MenuItem>
              <MenuItem value="Bus/Public">Bus/Public</MenuItem>
              <MenuItem value="Walk">Walk</MenuItem>
            </Select>
          </FormControl>
          <div>
          <Button
            variant="contained"
            color="primary"
            onClick={handleClose}
            sx={{ mt: 2 }}
          >
            Submit
          </Button>
          <Button
            variant="contained"
            color="primary"
            onClick={handleClose}
            sx={{ marginLeft: '25px', mt: 2 }}
          >
            Close
          </Button>
          </div>
        </Box>
      </Modal>
    </div>
  );
};

export default CustomModalAddNew;
