import React from 'react';
import { CssBaseline, Box } from '@mui/material';
import Header from './components/Header';
import MainContent from './components/MainContent';

const App = () => {
  const [openAdd, setOpenAdd] = React.useState(false);

  const closeOpen = () => {
    console.log('Close reach');
    setOpenAdd(true);
  }

  const closeAdd = () => {
    setOpenAdd(false);
  }
  return (
    <Box>
      <CssBaseline />
      <Header closeOpen={closeOpen}/>
      <Box
        component="main"
        sx={{ flexGrow: 1, p: 3 }}
      >
        <MainContent openAdd={openAdd} closeAdd={closeAdd}/>
      </Box>
    </Box>
  );
}

export default App;
