import React from "react";
import Button from "@mui/material/Button";

const ButtonJS = ({handleOpenBuyCredit, handleCloseBuyCredit, handleOpenOrganization, handleCloseOrganization}) => (
    <div>
    <div style={{ display: "flex", flexDirection: "row", padding: "0 3%", margin:'2% 0' }}>
      <div style={{ width: "20%" }}>
        <Button variant="contained" color="primary">
          {"Buy Credits"}
        </Button>
      </div>
      <div >
      <Button variant="contained" color="primary" onClick={handleOpenOrganization}>
          {"Organization"}
        </Button>
      </div>
    </div>
  </div>
);

export default ButtonJS;
